//feature wait for new cart totals to appear based on selectors being rendered after applying a code, if true resolve with the object returned to be pushed into the codesTried array, if the result message doesn't exist, return null in the price to know that we wouldn't be able to get the new cart total anyways, so best to setup error handling for that.
import getElement from './getElement';
export var waitForAmountSaved = function (selectorObj, startingCartValue) {
    return new Promise(function (resolve) {
        // const observer = new MutationObserver((mutations) => {
        // console.log("MUTATIONS IN waitForAmountSaved", mutations)
        //If these result message (error) or confirmed code applied DOM elements appear then resolve our promise
        if (getElement(selectorObj.cartTotalSelector)) {
            var currentCartValue = getElement(selectorObj.cartTotalSelector)
                .textContent.trim()
                .replace(/[^0-9.]/g, '');
            resolve((+startingCartValue - +currentCartValue).toFixed(2));
        }
        else {
            resolve('N/A');
        }
    });
};
