import isXPath from './isXpath';
export var waitForShopifyRemovalTag = function (removeCodeSelector) {
    return new Promise(function (resolve) {
        var observer = new MutationObserver(function (mutations) {
            mutations.forEach(function (mutation) {
                mutation.removedNodes.forEach(function (removedNode) {
                    if (removedNode instanceof Element &&
                        removedNode.querySelectorAll(removeCodeSelector).length > 0) {
                        resolve('found');
                        observer.disconnect();
                    }
                    else {
                        resolve('nothing found');
                        observer.disconnect();
                    }
                });
            });
        });
        observer.observe(document.body, {
            childList: true,
            subtree: true,
            characterData: true,
        });
    });
};
export var waitForShopifyAddingTag = function (removeCodeSelector, cartTotalSelector) {
    console.log('waitForShopifyAddingTag');
    return new Promise(function (resolve) {
        var observer = new MutationObserver(function (mutations) {
            mutations.forEach(function (mutation) {
                mutation.addedNodes.forEach(function (addedNode) {
                    if (isXPath(cartTotalSelector)) {
                        var totalSelectorChanged = document.evaluate(cartTotalSelector, addedNode, null, XPathResult.ORDERED_NODE_SNAPSHOT_TYPE, null);
                        if (totalSelectorChanged.snapshotLength > 0) {
                            resolve('found');
                            observer.disconnect();
                        }
                        else {
                            resolve('nothing found');
                            observer.disconnect();
                        }
                    }
                    if (isXPath(removeCodeSelector)) {
                        var removeCodeSelectorChanged = document.evaluate(removeCodeSelector, addedNode, null, XPathResult.ORDERED_NODE_SNAPSHOT_TYPE, null);
                        if (removeCodeSelectorChanged.snapshotLength > 0) {
                            resolve('found');
                            observer.disconnect();
                        }
                        else {
                            resolve('nothing found');
                            observer.disconnect();
                        }
                    }
                    if (!isXPath(removeCodeSelector) &&
                        !isXPath(cartTotalSelector) &&
                        addedNode instanceof Element &&
                        (addedNode.querySelectorAll(cartTotalSelector).length > 0 ||
                            addedNode.querySelectorAll(removeCodeSelector).length > 0)) {
                        resolve('found');
                        observer.disconnect();
                    }
                    else {
                        resolve('nothing found');
                        observer.disconnect();
                    }
                });
            });
        });
        observer.observe(document.body, {
            childList: true,
            subtree: true,
            characterData: true,
        });
    });
};
