/**
 * Determines whether the input string is a valid CSS selector expression by attempting
 * to query the DOM using the `document.querySelector()` method. Returns `true` if the
 * query is successful, indicating that the input string is a valid selector expression;
 * otherwise, returns `false`.
 *
 * @param input - The string to check for selector validity.
 * @returns `true` if the input string is a valid CSS selector expression; otherwise, `false`.
 */
export default function isSelector(input) {
    try {
        document.querySelector(input);
        return true;
    }
    catch (e) {
        return false;
    }
}
