var _a;
import render from './modalLayout';
import applyCoupons, { applyCode } from '../utils/applyCoupons';
import { TIMEOUTIDS } from '../../../utils/constants/app';
//START -------------- VARIABLES -------------
// --- Grab selectorObj from LocalStorage ---
var selectorObj = JSON.parse(localStorage.getItem('refermateSelectorObj'));
// --- Store Name ---
var storeName = selectorObj.storeName;
// --- Coupon Codes Array ---
var codesToTryArray = selectorObj.codesToTryArray;
// --- Cash Back ---
var cashback = selectorObj.store.cashback;
//See if Refermate is currently running
var codeApplyStatus = localStorage.getItem("refermateRunChecks_".concat(storeName));
//END -------------- VARIABLES -------------
//START -------------- MAIN FUNCTION -------------
var ApplyCodes = function (status) {
    console.log('ApplyCodes Called');
    var runningCheck = localStorage.getItem("refermateRunChecks_".concat(storeName));
    var codesTriedArray = JSON.parse(localStorage.getItem("refermateCodeResult_".concat(storeName))) || [];
    var resultsDetails = JSON.parse(localStorage.getItem("refermateCheckResults_".concat(storeName)));
    if (runningCheck === 'result') {
        render({
            type: 'result',
            storeName: selectorObj.storeName,
            codesToTryArray: codesToTryArray,
            cashback: selectorObj.store.cashback,
            codesTriedArray: codesTriedArray,
            bestDiscount: resultsDetails.code,
            domain: selectorObj.store.name,
            endingCartValue: resultsDetails.endingCartValue,
            storeId: selectorObj.store.store_id,
        });
        return;
    }
    else {
        render({
            type: 'apply',
            storeName: selectorObj.storeName,
            codesToTryArray: codesToTryArray,
            cashback: cashback,
            storeId: selectorObj.store.store_id,
        });
    }
    applyCoupons();
    var ids = JSON.parse(localStorage.getItem(TIMEOUTIDS)) || [];
    if (!localStorage.getItem("refermateCodeIndex_".concat(storeName)) ||
        ids.length > 0) {
        applyCode(status);
    }
};
_a = ApplyCodes;
$RefreshReg$(_a, "ApplyCodes");
//init the apply codes function
ApplyCodes(codeApplyStatus);
