/**
 * Determines whether the input string is a valid XPath expression by attempting
 * to evaluate it using the `document.evaluate()` method. Returns `true` if the
 * evaluation is successful, indicating that the input string is a valid XPath
 * expression; otherwise, returns `false`.
 *
 * @param input - The string to check for XPath expression validity.
 * @returns `true` if the input string is a valid XPath expression; otherwise, `false`.
 */
export default function isXPath(input) {
    try {
        document.evaluate(input, document, null, XPathResult.ANY_TYPE, null);
        return true;
    }
    catch (e) {
        return false;
    }
}
